import React, { Component, Fragment } from 'react';
import { Button, Collapse, Card, Drawer, Select, Input,
	Icon, Form, DatePicker } from 'antd';
import { strToObj, objToStr } from '../../common/CellUtil';
import formItemLayout from '../../configs/layout';
import { TRUE } from '../../common/Constants';
import './index.css';

const Panel = Collapse.Panel;
const Option = Select.Option;
const { TextArea } = Input;

class DropAreaEdit extends Component {
	constructor(props) {
		super(props);
		this.state = {
			options: [],
			breadcrumb: [],
		};
		this.handleSubmit = this.handleSubmit.bind(this);
		this.onAddOptionItem = this.onAddOptionItem.bind(this);
		this.onAddBreadcrumbItem = this.onAddBreadcrumbItem.bind(this);
		this.onDeleteComponent = this.onDeleteComponent.bind(this);
		this.onAddButton = this.onAddButton.bind(this);
		this.onDeleteBreadcrumbItem = this.onDeleteBreadcrumbItem.bind(this);
		this.onDeleteOptionItem = this.onDeleteOptionItem.bind(this);
		this.onDeleteButton = this.onDeleteButton.bind(this);
	}
	componentWillReceiveProps(nextProps) {
		const { dropConfig } = nextProps;
		const { type, options, btnArr, breadcrumb } = dropConfig;
		if (type === 'Option' && !this.initOptions) {
			this.setState({
				options,
			});
			this.optionIndex = options.length;
			this.initOptions = true;
		}
		if (type === 'Btn' && !this.initBtn) {
			this.setState({
				btnArr,
			});
			this.btnIndex = btnArr.length;
			this.initBtn = true;
		}
		if (type === 'Breadcrumb' && !this.initBreadcrumb) {
			this.setState({
				breadcrumb,
			});
			this.breadcrumbIndex = breadcrumb.length;
			this.initBreadcrumb = true;
		}
	}
	handleSubmit(e) {
		e.preventDefault();
		const { options, btnArr, breadcrumb } = this.state;
		const { form, onUpdateConfigs, dropConfig } = this.props;
		form.validateFields((err, values) => {
	        if (!err) {
				const newdropConfig = Object.assign({}, dropConfig, values);
				newdropConfig.cellStyles = strToObj(values.cellStyles);
				if (newdropConfig.type === 'Select') {
					const newOptions = options.slice();
					newOptions.forEach((item) => {
						const { index } = item;
						const kText = `optionText-${index}`;
						const kValue = `optionValue-${index}`;
						item.optionText = values[kText];
						item.optionValue= values[kValue];
						delete newdropConfig[kText];
						delete newdropConfig[kValue];
					});
					newdropConfig.options = newOptions;
				} else if (newdropConfig.type === 'DatePicker') {
					newdropConfig.defaultValue = values.defaultValue && values.defaultValue.format('YYYY-MM-DD');
				} else if (newdropConfig.type === 'Btn') {
					const newBtnArr = btnArr.slice();
					newBtnArr.forEach((item) => {
						const { index } = item;
						const btnText = `btnText-${index}`;
						const btnType = `type-${index}`;
						const htmlType = `htmlType-${index}`;
						const onClick = `onClick-${index}`;
						const expandFlag = `expandFlag-${index}`;
						const btnStyle = `btnStyle-${index}`;
						item.btnText = values[btnText];
						item.type= values[btnType];
						item.htmlType = values[htmlType];
						item.expandFlag = values[expandFlag];
						item.style = strToObj(values[btnStyle]);
						if (item.htmlType !== 'submit') {
							item.onClick = values[onClick];
						}
						const expandCount = `expandCount-${index}`;
						if (expandFlag === TRUE) {
							item.expandCount = values[expandCount];
						}
						delete newdropConfig[btnStyle];
						delete newdropConfig[expandCount];
						delete newdropConfig[expandFlag];
						delete newdropConfig[onClick];
						delete newdropConfig[htmlType];
						delete newdropConfig[btnText];
						delete newdropConfig[btnType];
					});
					newdropConfig.btnArr = newBtnArr;
				} else if (newdropConfig.type === 'Breadcrumb') {
					const newBreadcrumb = breadcrumb.slice();
					newBreadcrumb.forEach((item) => {
						const { index } = item;
						const breadcrumbLinkUrl = `breadcrumbLinkUrl-${index}`;
						const breadcrumbText = `breadcrumbText-${index}`;
						item.linkUrl = values[breadcrumbLinkUrl];
						item.text= values[breadcrumbText];
						delete newdropConfig[breadcrumbLinkUrl];
						delete newdropConfig[breadcrumbText];
					});
					newdropConfig.breadcrumb = newBreadcrumb;
				}
				onUpdateConfigs(newdropConfig);
	        }
	    });
	}
	onAddOptionItem() {
		const { options = [] } = this.state;
		const newOptions = options.slice();
		newOptions.push({
			value: '',
			text: '',
			index: this.optionIndex,
		});
		this.setState({
			options: newOptions,
		});
		this.optionIndex += 1;
	}
	onAddBreadcrumbItem() {
		const { breadcrumb = [] } = this.state;
		const newBreadcrumb = breadcrumb.slice();
		newBreadcrumb.push({
			text: '',
			linkUrl: '',
			index: this.breadcrumbIndex,
		});
		this.setState({
			breadcrumb: newBreadcrumb,
		});
		this.breadcrumbIndex += 1;
	}
	onAddButton() {
		const { btnArr } = this.state;
		const newBtnArr = btnArr.slice();
		newBtnArr.push({
			btnText: 'button',
			type: 'primary',
			index: this.btnIndex,
		});
		this.setState({
			btnArr: newBtnArr,
		});
		this.btnIndex += 1;
	}
	onDeleteButton(index) {
		const { btnArr = [] } = this.state;
		const newBtnArr = btnArr.reduce((arr, item) => {
			if (item.index !== index) {
				arr.push(item);
			}
			return arr;
		}, []);
		this.setState({
			btnArr: newBtnArr,
		});
	}
	onDeleteOptionItem(index) {
		const { options = [] } = this.state;
		const newOptions = options.reduce((arr, item) => {
			if (item.index !== index) {
				arr.push(item);
			}
			return arr;
		}, []);
		this.setState({
			options: newOptions,
		});
	}
	onDeleteBreadcrumbItem(index) {
		const { breadcrumb = [] } = this.state;
		const newBreadcrumb = breadcrumb.reduce((arr, item) => {
			if (item.index !== index) {
				arr.push(item);
			}
			return arr;
		}, []);
		this.setState({
			breadcrumb: newBreadcrumb,
		});
	}
	onDeleteComponent() {
		const { onUpdateConfigs, dropConfig } = this.props;
		const newDropConfig = Object.assign({}, dropConfig, { deleteFlag: true });
		onUpdateConfigs(newDropConfig);
	}
	renderOptionsCard() {
		const { form } = this.props;
		const { options } = this.state;
		return (
			<Card
				title="option设置"
				extra={<Icon type="plus-circle" onClick={this.onAddOptionItem} />}
			>
				{
					options.map(({ value, text, index }) => {
						return (
							<Fragment key={`optionitem-${index}`}>
								<Form.Item
									key={`optionitem-value-${index}`}
									style={{ width: '45%', display: 'inline-block' }} 
								>
									{form.getFieldDecorator(`optionValue-${index}`, {
										initialValue: value,
									})(
										<Input placeholder="value" />
									)}
								</Form.Item>
								<Form.Item
									key={`optionitem-text-${index}`}
									style={{ width: '45%', display: 'inline-block', marginLeft: '10px' }} 
								>
									{form.getFieldDecorator(`optionText-${index}`, {
										initialValue: text,
									})(
										<Input placeholder="show text" />
									)}
								</Form.Item>
								<Icon
									type="close-circle"
									style={{ marginLeft: '6px', lineHeight: '39px' }}
									onClick={() => {
										this.onDeleteOptionItem(index);
									}}
								/>
							</Fragment>
						);
					})
				}
			</Card>
		);
	}
	renderPanelHeader({ btnText, index }) {
        return (
            <div className="panel-header">
                <span className="title">{btnText}</span>
                <Icon
                    type="close"
                    className="btn-delete"
                    onClick={() => {
                        this.onDeleteButton(index);
                    }}
                />
            </div>
        );
    }
	renderBtnCard() {
		const { dropConfig, form } = this.props;
		const { type } = dropConfig;
		const { btnArr } = this.state;
		if (type === 'Btn') {
			return (
				<Card
					title="按钮区域"
					extra={
						<Icon type="plus-circle" onClick={this.onAddButton} />
					}
				>
					<Collapse>
					{
						btnArr.map((item) => {
							const { index, btnText, type, htmlType = 'button',
								onClick, btnStyle } = item;
							return (
								<Panel header={this.renderPanelHeader(item)} key={`btn-${index}`}>
									<Form.Item
										key={`btnText-${index}`}
										label="按钮文字"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`btnText-${index}`, {
											initialValue: btnText,
										})(
											<Input placeholder="btnText" />
										)}
									</Form.Item>
									<Form.Item
										key={`type-${index}`}
										label="type"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`type-${index}`, {
											initialValue: type,
										})(
											<Select>
												<Option value="primary">primary</Option>
												<Option value="dashed">dashed</Option>
												<Option value="danger">danger</Option>
												<Option value="">none</Option>
											</Select>
										)}
									</Form.Item>
									<Form.Item
										key={`htmlType-${index}`}
										label="htmlType"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`htmlType-${index}`, {
											initialValue: htmlType,
										})(
											<Select>
												<Option value="submit">submit</Option>
												<Option value="reset">reset</Option>
												<Option value="button">button</Option>
											</Select>
										)}
									</Form.Item>
									<Form.Item
										key={`onClick-${index}`}
										label="onClick"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`onClick-${index}`, {
											initialValue: onClick,
										})(
											<Input placeholder="点击事件名称" />
										)}
									</Form.Item>
									<Form.Item
										key={`btnStyle-${index}`}
										label="按钮样式"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`btnStyle-${index}`, {
											initialValue: btnStyle,
										})(
											<TextArea placeholder="多个样式属性请用分号分隔" />
										)}
									</Form.Item>
								</Panel>
							);
						})
					}
					</Collapse>
				</Card>
			);
		}
		return null;
	}
	renderBreadcrumb() {
		const { form } = this.props;
		const { breadcrumb } = this.state;
		return (
			<Card
				title="面包屑设置"
				extra={<Icon type="plus-circle" onClick={this.onAddBreadcrumbItem} />}
			>
				{
					breadcrumb.map(({ text, linkUrl, index }) => {
						return (
							<Fragment key={`breadcrumb-item-${index}`}>
								<Form.Item
									key={`breadcrumbitem-value-${index}`}
									style={{ width: '45%', display: 'inline-block' }} 
								>
									{form.getFieldDecorator(`breadcrumbText-${index}`, {
										initialValue: text,
									})(
										<Input placeholder="导航名称" />
									)}
								</Form.Item>
								<Form.Item
									key={`breadcrumbitem-text-${index}`}
									style={{ width: '45%', display: 'inline-block', marginLeft: '10px' }} 
								>
									{form.getFieldDecorator(`breadcrumbLinkUrl-${index}`, {
										initialValue: linkUrl,
									})(
										<Input placeholder="导航链接" />
									)}
								</Form.Item>
								<Icon
									type="close-circle"
									style={{ marginLeft: '6px', lineHeight: '39px' }}
									onClick={() => {
										this.onDeleteBreadcrumbItem(index);
									}}
								/>
							</Fragment>
						);
					})
				}
			</Card>
		);
	}
	renderDefaultValueItem() {
		const { dropConfig, form } = this.props;
		const { type, defaultValue, text } = dropConfig;
		const { getFieldDecorator } = form;
		const { options } = this.state;
		if (type === 'Select') {
			return (
				<Form.Item {...formItemLayout} label="defaultValue">
					{getFieldDecorator('defaultValue', {
						initialValue: defaultValue,
					})(
						<Select>
						{
							options.map((item) => {
								const { index } = item;
								const optionValue = form.getFieldValue(`optionValue-${index}`);
								const optionText = form.getFieldValue(`optionText-${index}`);
								return (
									<Option value={optionValue} key={`op-${index}`}>
										{`${optionValue}/${optionText}`}
									</Option>
								);
							})
						}
					</Select>
					)}
				</Form.Item>
			);
		} else if (type === 'Text') {
			return (<Form.Item {...formItemLayout} label="文字">
				{getFieldDecorator('text', {
					initialValue: text,
				})(
					<Input />
				)}
			</Form.Item>);
		} else if (type === 'DatePicker') {
			return (<Form.Item {...formItemLayout} label="defaultValue">
				{getFieldDecorator('defaultValue', {
					initialValue: defaultValue,
				})(
					<DatePicker style={{ width: '100%' }} />
				)}
			</Form.Item>);
		} else if (type === 'Input') {
			return (<Form.Item {...formItemLayout} label="defaultValue">
					{getFieldDecorator('defaultValue', {
						initialValue: defaultValue,
					})(
						<Input />
					)}
				</Form.Item>);
		} else {
			return null;
		}
	}
	renderContent() {
		const { form, dropConfig } = this.props;
		const { placeholder, type } = dropConfig;
        const { getFieldDecorator } = form;
        // 有拖拽的组件&&没有删除组件
		if (type === 'Btn') {
			return this.renderBtnCard();
		}
		if (type === 'Breadcrumb') {
			return this.renderBreadcrumb();
		}
		return (
			<Fragment>
				{this.renderDefaultValueItem()}
				{
					type !== 'Text' && type !== 'Breadcrumb' ?
						(<Form.Item {...formItemLayout} label="placeholder">
							{getFieldDecorator('placeholder', {
								initialValue: placeholder,
							})(
								<Input />
							)}
						</Form.Item>) : null
				}
				{
					type === 'Select' ? 
						this.renderOptionsCard() : null
				}
			</Fragment>
		);
	}
	render() {
		const { visible, onClose, form, dropConfig } = this.props;
		const initialCellStyles = objToStr(dropConfig.cellStyles);
		return (
			<Drawer
				visible={visible}
				title="编辑组件"
				onClose={() => {
					this.initOptions = false;
					this.initBtn = false;
					this.initBreadcrumb = false;
					onClose();
				}}
				width={420}
			>
				<Form onSubmit={this.handleSubmit}>
					<Form.Item label="style设置" {...formItemLayout}>
						{form.getFieldDecorator("cellStyles", {
							initialValue: initialCellStyles,
						})(
							<TextArea placeholder="多个样式属性请用分号分隔" />
						)}
					</Form.Item>
                    {
                        this.renderContent()
                    }
                    <div className="btn-box">
						<Button onClick={this.onDeleteComponent}>
							删除组件
						</Button>
					</div>
			        <div className="btn-bottom-box">
						<Button type="primary" htmlType="submit">
							保存
						</Button>
			        </div>
		    	</Form>
	    	</Drawer>
	    );
	}
}

export default Form.create()(DropAreaEdit);