import axios from 'axios';

// const HOST = 'http://127.0.0.1:8888';

class AppService {
    generatePage(data) {
        return axios({
            method: 'POST',
            url: `/generatePage`,
            data,
            // headers: {
            //     'Content-Type': 'application/form-data'
            // }
        });
    }
}

export default AppService;