import React, { Fragment } from 'react';
import { Button, Input, Select, DatePicker, Breadcrumb } from 'antd';

const { Option } = Select;
const { RangePicker } = DatePicker;

const renderComponent = (dropConfig) => {
    const { placeholder = '', type = 'Input' } = dropConfig;
    let component = null;
    switch(type) {
        case 'Text':
            component = renderText(dropConfig);
            break;
        case 'Breadcrumb':
            component = renderBreadcrumb(dropConfig);
            break;
        case 'Select':
            component = renderSelect(dropConfig);
            break;
        case 'RangePicker':
            component = renderRangePicker(dropConfig);
            break;
        case 'DatePicker':
            component = renderDatePicker(dropConfig);
            break;
        case 'Btn':
            component = renderBtn(dropConfig);
            break;
        default:
            component = (<Input placeholder={placeholder} />);
            break;
    }
    return component;
};
const renderBreadcrumb = (config) => {
    const { breadcrumb = [], style } = config;
    return (
        <Breadcrumb style={style}>
            {
                breadcrumb.map(({ text, linkUrl }, i) => {
                    return (
                        <Breadcrumb.Item key={`breadcrumb-${i}`}>
                            { linkUrl ? 
                                <a href={linkUrl}>{text}</a> : <span>{text}</span>
                            }
                        </Breadcrumb.Item>
                    );
                })
            }
        </Breadcrumb>
    )
};
const renderText = (config) => {
    const { text, style } = config;
    return (
        <span style={style}>{text}</span>
    );
};

const renderSelect = (selectConfig) => {
    const { style = {}, options = [] } = selectConfig;
    return (
        <Select style={{ width: '100%', ...style }}>
            {
                Array.isArray(options) ? options.map(({ key, optionValue, optionText }) => {
                    return (
                        <Option
                            key={key || optionValue}
                            value={optionValue}
                        >
                            {optionText}
                        </Option>
                    );
                }) : null
            }
        </Select>
    );
};
const renderDatePicker = () => {
    return (
        <DatePicker style={{ width: '100%' }} />
    );
};
const renderRangePicker = () => {
    return (
        <RangePicker style={{ width: '100%' }} />
    );
}

const renderBtn = (itemConfig) => {
    const { btnArr } = itemConfig;
    return (
        <Fragment>
            {
                btnArr.map((btn) => {
                    const { btnText = 'button', expandFlag, expandCount,
                        style = {}, index, ...btnProps } = btn;
                    return (
                        <Button
                            key={`btn-${index}`}
                            style={style}
                            {...btnProps}
                        >
                            {btnText}
                        </Button>
                    );
                })
            }
        </Fragment>
    );
}

export default renderComponent;