import React, { Component, Fragment } from 'react';
import { Button, Collapse, Card, Drawer, Divider, Select, Input,
	Icon, Form, DatePicker, Radio } from 'antd';
import getCellColSpan, { strToObj, objToStr } from '../../common/CellUtil';
import { TRUE } from '../../common/Constants';
import './index.css';

const Panel = Collapse.Panel;
const Option = Select.Option;
const { TextArea } = Input;

const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
};

class FormItemEdit extends Component {
	constructor(props) {
		super(props);
		this.state = {
			deleteComponent: false,
			options: [],
			// cellStyles: [],
		};
		this.onSubmit = this.onSubmit.bind(this);
		this.onAddOptionItem = this.onAddOptionItem.bind(this);
		this.onAddCellStyleItem = this.onAddCellStyleItem.bind(this);
		this.onAddButton = this.onAddButton.bind(this);
		this.onDeleteCellStyleItem = this.onDeleteCellStyleItem.bind(this);
		this.onDeleteOptionItem = this.onDeleteOptionItem.bind(this);
		this.onDeleteButton = this.onDeleteButton.bind(this);
		this.onDeleteComponent = this.onDeleteComponent.bind(this);
	}
	componentWillReceiveProps(nextProps) {
		const { formItemConfig } = nextProps;
		const { type, options, btnArr } = formItemConfig;
		if (type === 'Option' && !this.initOptions) {
			this.setState({
				options,
			});
			this.optionIndex = options.length;
			this.initOptions = true;
		}
		// if (Array.isArray(cellStyles)  && !this.initCellStyles) {
		// 	this.setState({
		// 		cellStyles,
		// 	});
		// 	this.cellStyleIndex = cellStyles.length;
		// 	this.initCellStyles = true;
		// }
		if (type === 'Btn' && !this.initBtn) {
			this.setState({
				btnArr,
			});
			this.btnIndex = btnArr.length;
			this.initBtn = true;
		}
	}
	componentDidMount() {
		// 监控撤销删除组件操作 ctrl + z
	}
	onSubmit(e) {
		e.preventDefault();
		e.stopPropagation();
		const { deleteComponent, options, btnArr } = this.state;
		const { form, onUpdateConfigs, formItemConfig, colSpanArr, formItemArr } = this.props;
		form.validateFields((err, values) => {
	        if (!err) {
				const newFormItemConfig = Object.assign({}, formItemConfig, values);
				if (deleteComponent) {
					newFormItemConfig.deleteFlag = true;
				}
				// const { cellStyles = [] } = this.state;
				newFormItemConfig.cellStyles = strToObj(values.cellStyles);
				if (newFormItemConfig.type === 'Select') {
					const newOptions = options.slice();
					newOptions.forEach((item) => {
						const { index } = item;
						const kText = `optionText-${index}`;
						const kValue = `optionValue-${index}`;
						item.optionText = values[kText];
						item.optionValue= values[kValue];
						delete newFormItemConfig[kText];
						delete newFormItemConfig[kValue];
					});
					newFormItemConfig.options = newOptions;
				} else if (newFormItemConfig.type === 'DatePicker') {
					newFormItemConfig.defaultValue = values.defaultValue && values.defaultValue.format('YYYY-MM-DD');
				} else if (newFormItemConfig.type === 'Btn') {
					const newBtnArr = btnArr.slice();
					newBtnArr.forEach((item) => {
						const { index } = item;
						const btnText = `btnText-${index}`;
						const btnType = `type-${index}`;
						const htmlType = `htmlType-${index}`;
						const onClick = `onClick-${index}`;
						const expandFlag = `expandFlag-${index}`;
						const btnStyle = `btnStyle-${index}`;
						item.btnText = values[btnText];
						item.type= values[btnType];
						item.htmlType = values[htmlType];
						item.expandFlag = values[expandFlag];
						item.style = strToObj(values[btnStyle]);
						if (item.htmlType !== 'submit') {
							item.onClick = values[onClick];
						}
						const expandCount = `expandCount-${index}`;
						if (expandFlag === TRUE) {
							item.expandCount = values[expandCount];
						}
						delete newFormItemConfig[btnStyle];
						delete newFormItemConfig[expandCount];
						delete newFormItemConfig[expandFlag];
						delete newFormItemConfig[onClick];
						delete newFormItemConfig[htmlType];
						delete newFormItemConfig[btnText];
						delete newFormItemConfig[btnType];
					});
					newFormItemConfig.btnArr = newBtnArr;
				}
				const { originSpan = 1 } = values;
				newFormItemConfig.originSpan = originSpan;
				newFormItemConfig.colSpan = getCellColSpan(formItemConfig.colIndex, originSpan,
					formItemArr, colSpanArr);
				onUpdateConfigs(newFormItemConfig);
	        }
	      },
	    );
	}
	onDeleteComponent() {
		this.setState({
			deleteComponent: true,
		});
	}
	onAddOptionItem() {
		const { options = [] } = this.state;
		const newOptions = options.slice();
		newOptions.push({
			value: '',
			text: '',
			index: this.optionIndex,
		});
		this.setState({
			options: newOptions,
		});
		this.optionIndex += 1;
	}
	onAddCellStyleItem() {
		const { cellStyles = [] } = this.state;
		const newCellStyles = cellStyles.slice();
		newCellStyles.push({
			value: '',
			name: '',
			index: this.cellStyleIndex,
		});
		this.setState({
			cellStyles: newCellStyles,
		});
		this.cellStyleIndex += 1;
	}
	onAddButton() {
		const { btnArr } = this.state;
		const newBtnArr = btnArr.slice();
		newBtnArr.push({
			btnText: 'button',
			type: 'primary',
			index: this.btnIndex,
		});
		this.setState({
			btnArr: newBtnArr,
		});
		this.btnIndex += 1;
	}
	onDeleteButton(index) {
		const { btnArr = [] } = this.state;
		const newBtnArr = btnArr.reduce((arr, item) => {
			if (item.index !== index) {
				arr.push(item);
			}
			return arr;
		}, []);
		this.setState({
			btnArr: newBtnArr,
		});
	}
	onDeleteCellStyleItem(index) {
		const { cellStyles = [] } = this.state;
		const newCellStyles = cellStyles.reduce((arr, item) => {
			if (item.index !== index) {
				arr.push(item);
			}
			return arr;
		}, []);
		this.setState({
			cellStyles: newCellStyles,
		});
	}
	onDeleteOptionItem(index) {
		const { options = [] } = this.state;
		const newOptions = options.reduce((arr, item) => {
			if (item.index !== index) {
				arr.push(item);
			}
			return arr;
		}, []);
		this.setState({
			options: newOptions,
		});
	}
	renderOptionsCard() {
		const { form } = this.props;
		const { options } = this.state;
		return (
			<Card
				title="option设置"
				extra={<Icon type="plus-circle" onClick={this.onAddOptionItem} />}
			>
				{
					options.map(({ value, text, index }) => {
						return (
							<Fragment key={`optionitem-${index}`}>
								<Form.Item
									key={`optionitem-value-${index}`}
									style={{ width: '45%', display: 'inline-block' }} 
								>
									{form.getFieldDecorator(`optionValue-${index}`, {
										initialValue: value,
									})(
										<Input placeholder="value" />
									)}
								</Form.Item>
								<Form.Item
									key={`optionitem-text-${index}`}
									style={{ width: '45%', display: 'inline-block', marginLeft: '10px' }} 
								>
									{form.getFieldDecorator(`optionText-${index}`, {
										initialValue: text,
									})(
										<Input placeholder="show text" />
									)}
								</Form.Item>
								<Icon
									type="close-circle"
									style={{ marginLeft: '6px', lineHeight: '39px' }}
									onClick={() => {
										this.onDeleteOptionItem(index);
									}}
								/>
							</Fragment>
						);
					})
				}
			</Card>
		);
	}
	renderPanelHeader({ btnText, index }) {
        return (
            <div className="panel-header">
                <span className="title">{btnText}</span>
                <Icon
                    type="close"
                    className="btn-delete"
                    onClick={() => {
                        this.onDeleteButton(index);
                    }}
                />
            </div>
        );
    }
	renderBtnCard() {
		const { formItemConfig, form } = this.props;
		const { type } = formItemConfig;
		const { btnArr, expandFlag = '0' } = this.state;
		if (type === 'Btn') {
			return (
				<Card
					title="按钮区域"
					extra={
						<Icon type="plus-circle" onClick={this.onAddButton} />
					}
				>
					<Collapse>
					{
						btnArr.map((item) => {
							const { index, btnText, type, htmlType = 'button',
								onClick, expandCount, btnStyle } = item;
							return (
								<Panel header={this.renderPanelHeader(item)} key={`btn-${index}`}>
									<Form.Item
										key={`btnText-${index}`}
										label="按钮文字"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`btnText-${index}`, {
											initialValue: btnText,
										})(
											<Input placeholder="btnText" />
										)}
									</Form.Item>
									<Form.Item
										key={`type-${index}`}
										label="type"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`type-${index}`, {
											initialValue: type,
										})(
											<Select>
												<Option value="primary">primary</Option>
												<Option value="dashed">dashed</Option>
												<Option value="danger">danger</Option>
												<Option value="">none</Option>
											</Select>
										)}
									</Form.Item>
									<Form.Item
										key={`htmlType-${index}`}
										label="htmlType"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`htmlType-${index}`, {
											initialValue: htmlType,
										})(
											<Select>
												<Option value="submit">submit</Option>
												<Option value="reset">reset</Option>
												<Option value="button">button</Option>
											</Select>
										)}
									</Form.Item>
									<Form.Item
										key={`onClick-${index}`}
										label="onClick"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`onClick-${index}`, {
											initialValue: onClick,
										})(
											<Input placeholder="点击事件名称" />
										)}
									</Form.Item>
									<Form.Item
										key={`btnStyle-${index}`}
										label="按钮样式"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`btnStyle-${index}`, {
											initialValue: btnStyle,
										})(
											<TextArea placeholder="多个样式属性请用分号分隔" />
										)}
									</Form.Item>
									<Form.Item
										key={`expandFlag-${index}`}
										label="折叠"
										{...formItemLayout}
									>
										{form.getFieldDecorator(`expandFlag-${index}`, {
											initialValue: expandFlag,
										})(
											<Select>
												<Option value="0">否</Option>
												<Option value="1">是</Option>
											</Select>
										)}
									</Form.Item>
									{
										form.getFieldValue(`expandFlag-${index}`) === '1' ?
											(<Form.Item
												key={`expandCount-${index}`}
												label="折叠可见数"
												{...formItemLayout}
											>
												{form.getFieldDecorator(`expandCount-${index}`, {
													initialValue: expandCount,
												})(
													<Input placeholder="expandCount" />
												)}
											</Form.Item>) : null
									}
								</Panel>
							);
						})
					}
					</Collapse>
				</Card>
			);
		}
		return null;
	}
	renderDefaultValueItem() {
		const { formItemConfig, form } = this.props;
		const { type, defaultValue } = formItemConfig;
		const { getFieldDecorator } = form;
		const { options } = this.state;
		if (type === 'Select') {
			return (
				<Form.Item {...formItemLayout} label="defaultValue">
					{getFieldDecorator('defaultValue', {
						initialValue: defaultValue,
					})(
						<Select>
						{
							options.map((item) => {
								const { index } = item;
								const optionValue = form.getFieldValue(`optionValue-${index}`);
								const optionText = form.getFieldValue(`optionText-${index}`);
								return (
									<Option value={optionValue} key={`op-${index}`}>
										{`${optionValue}/${optionText}`}
									</Option>
								);
							})
						}
					</Select>
					)}
				</Form.Item>
			);
		} else if (type === 'DatePicker') {
			return (<Form.Item {...formItemLayout} label="defaultValue">
				{getFieldDecorator('defaultValue', {
					initialValue: defaultValue,
				})(
					<DatePicker style={{ width: '100%' }} />
				)}
			</Form.Item>);
		} else if (type === 'Input') {
			return (<Form.Item {...formItemLayout} label="defaultValue">
					{getFieldDecorator('defaultValue', {
						initialValue: defaultValue,
					})(
						<Input />
					)}
				</Form.Item>);
		} else {
			return null;
		}
	}
	renderLinkage(formItemConfig, formItemArr) {
		const { condition, linkName } = formItemConfig;
		const { getFieldDecorator } = this.props.form;
		return (
			<Fragment>
				<Form.Item {...formItemLayout} label="关联组件name">
					{getFieldDecorator('linkName', {
						initialValue: linkName,
					})(
						<Select>
							{
								formItemArr.map(({ name, colIndex, label }) => {
									if (name && colIndex !== formItemConfig.colIndex) {
										return (<Option value={`${name}`}>{`${name}/${label}`}</Option>);
									}
									return null;
								})
							}
						</Select>
					)}
				</Form.Item>
				<Form.Item {...formItemLayout} label="condition">
					{getFieldDecorator('condition', {
						initialValue: condition,
					})(
						<Input />
					)}
				</Form.Item>
			</Fragment>
		);
	}
	renderContent() {
		const { deleteComponent } = this.state;
		const { form, dropComponent = false, formItemConfig, formItemArr } = this.props;
		const { label, name, placeholder, type, linkage } = formItemConfig;
		const { getFieldDecorator } = form;
		if (dropComponent && !deleteComponent) {
			if (type === 'Btn') {
				return this.renderBtnCard();
			}
			return (<Fragment>
				<Divider>组件</ Divider>
				<Form.Item {...formItemLayout} label="label">
					{getFieldDecorator('label', {
						initialValue: label,
					})(
						<Input />
					)}
				</Form.Item>
				<Form.Item {...formItemLayout} label="name">
					{getFieldDecorator('name', {
						initialValue: name,
					})(
						<Input />
					)}
				</Form.Item>
				{this.renderDefaultValueItem()}
				<Form.Item {...formItemLayout} label="placeholder">
					{getFieldDecorator('placeholder', {
						initialValue: placeholder,
					})(
						<Input />
					)}
				</Form.Item>
				<Form.Item {...formItemLayout} label="联动关联">
					{getFieldDecorator('hasLinkage', {
						initialValue: linkage,
					})(
						<Select>
							<Option value="0">否</Option>
							<Option value="1">是</Option>
						</Select>
					)}
				</Form.Item>
				{
					form.getFieldValue('hasLinkage') === '1' ?
						this.renderLinkage(formItemConfig, formItemArr) : null
				}
				{
					type === 'Select' ? 
						this.renderOptionsCard() : null
				}
				<Form.Item style={{ textAlign: 'center' }}>
					<Button onClick={this.onDeleteComponent}>
						删除组件
					</Button>
				</Form.Item>
			</Fragment>);
		}
		return null;
	}
	renderCellStylesSetting() {
		const { form } = this.props;
		const { cellStyles } = this.state;
		return (
			<Card
				title="style设置"
				extra={<Icon type="plus-circle" onClick={this.onAddCellStyleItem} />}
			>
				{
					cellStyles.map(({ name, value, index }) => {
						return (
							<Fragment key={`cell-styleitem-${index}`}>
								<Form.Item
									key={`cell-styleitem-name-${index}`}
									style={{ width: '45%', display: 'inline-block' }} 
								>
									{form.getFieldDecorator(`styleName-${index}`, {
										initialValue: name,
									})(
										<Input placeholder="style name" />
									)}
								</Form.Item>
								<Form.Item
									key={`cell-styleitem-value-${index}`}
									style={{ width: '45%', display: 'inline-block', marginLeft: '10px' }} 
								>
									{form.getFieldDecorator(`styleValue-${index}`, {
										initialValue: value,
									})(
										<Input placeholder="style value" />
									)}
								</Form.Item>
								<Icon
									type="close-circle"
									style={{ marginLeft: '6px', lineHeight: '39px' }}
									onClick={() => {
										this.onDeleteCellStyleItem(index);
									}}
								/>
							</Fragment>
						);
					})
				}
			</Card>
		);
	}
	render() {
		const { visible, onClose, form, onDeleteFormItemContainer,
			formItemConfig, colSpanArr } = this.props;
		let initialColSpan = 1;
		let tempColSpan = formItemConfig.colSpan;
		for (let i = 0; i < colSpanArr.length; i++) {
			tempColSpan -= colSpanArr[i].colSpan;
			if (tempColSpan > 0) {
				initialColSpan++;
			} else {
				i = colSpanArr.length;
			}
		}
		const initialCellStyles = objToStr(formItemConfig.cellStyles);
		return (
			<Drawer
				visible={visible}
				title="编辑表单项"
				onClose={() => {
					this.setState({
						deleteComponent: false,
					});
					this.initOptions = false;
					this.initBtn = false;
					onClose();
				}}
				width={420}
			>
				<Form onSubmit={this.onSubmit}>
				<Divider>单元格</ Divider>
					{/* {this.renderCellStylesSetting()} */}
					<Form.Item
						style={{ padding: '0 24px' }}
						label="style设置"
					>
						{form.getFieldDecorator("cellStyles", {
							initialValue: initialCellStyles,
						})(
							<TextArea placeholder="多个样式属性请用分号分隔" />
						)}
					</Form.Item>
					<Form.Item key="cell-colSpan" label="跨列设置" style={{ padding: '0 24px' }}>
						{form.getFieldDecorator("originSpan", {
							initialValue: initialColSpan,
						})(
							<Select>
								{
									colSpanArr.map((c, i) => {
										return (
											<Option
												key={`${c}-${i}`}
												value={i+1}
											>
												{`${i+1}列`}
											</Option>
										);
									})
								}
							</Select>
						)}
					</Form.Item>
					<div className="btn-box">
						<Button onClick={onDeleteFormItemContainer}>
							删除单元格
						</Button>
					</div>
                    {
                        this.renderContent()
                    }
			        <div className="btn-bottom-box">
						<Button type="primary" htmlType="submit">
							保存
						</Button>
			        </div>
		    	</Form>
	    	</Drawer>
	    );
	}
}

export default Form.create()(FormItemEdit);