import React, { Component, Fragment } from 'react';
import { Button, Input, Icon, Form, Col, Select, DatePicker } from 'antd';
import { TRUE } from '../common/Constants';
import moment  from 'moment';

const { Option } = Select;
const { RangePicker } = DatePicker;  

class Editable extends Component {
	getType() {
		return '';
	}
	constructor(props) {
		super(props);
		this.state = {
			editable: false, //可编辑标识
		};
		this.onEditToggle = this.onEditToggle.bind(this);
	}
	onEditToggle(e) {
        e && e.stopPropagation();
		const { editable } = this.state;
		this.setState({
			editable: !editable,
		});
	}
	renderItem = (itemConfig) => {
        const { placeholder, type = 'Input' } = itemConfig;
        let itemComponent = null;
        switch(type) {
            case 'Select':
                itemComponent = this.renderSelect(itemConfig);
                break;
            case 'RangePicker':
                itemComponent = this.renderRangePicker(itemConfig);
                break;
            case 'DatePicker':
                itemComponent = this.renderDatePicker(itemConfig);
                break;
            case 'Btn':
                itemComponent = this.renderBtn(itemConfig);
                break;
            default:
                itemComponent = (<Input placeholder={placeholder} />);
                break;
        }
        return itemComponent;
    }
    renderSelect = (selectConfig) => {
        const { style = {}, options = [] } = selectConfig;
        return (
            <Select style={{ width: '100%', ...style }}>
                {
                    Array.isArray(options) ? options.map(({ key, optionValue, optionText }) => {
                        return (
                            <Option
                                key={key || optionValue}
                                value={optionValue}
                            >
                                {optionText}
                            </Option>
                        );
                    }) : null
                }
            </Select>
        );
    }

    renderDatePicker = () => {
        return (
            <DatePicker style={{ width: '100%' }} />
        );
    }

    renderRangePicker = () => {
        return (
            <RangePicker style={{ width: '100%' }} />
        );
    }

    renderBtn = (itemConfig) => {
        const { btnArr } = itemConfig;
        return (
            <Fragment>
                {
                    btnArr.map((btn) => {
                        const { btnText = 'button', expandFlag, expandCount,
                            style = {}, index, ...btnProps } = btn;
                        if (expandFlag === TRUE) {
                            const { expand, onCollapse } = this.props;
                            const collapseBtnText = expand ? '隐藏' : '展开';
                            return (
                                <a
                                    className="btn-collapse"
                                    onClick={onCollapse}
                                    href="&nbsp;"
                                    key={`btn-${index}`}
                                    style={style}
                                >
                                    {collapseBtnText}
                                    <Icon type={expand ? 'up' : 'down'} />
                                </a>
                                );
                        }
                        return (
                            <Button
                                key={`btn-${index}`}
                                style={style}
                                {...btnProps}
                            >
                                {btnText}
                            </Button>
                        );
                    })
                }
            </Fragment>
        );
    }

    getInitialValue(formItemConfig) {
        const { type = 'Input', defaultValue } = formItemConfig;
        if (type === 'DatePicker' || type === 'RangePicker') {
            return defaultValue && moment(defaultValue, 'YYYY-MM-DD');
        }
        return formItemConfig.defaultValue || '';
    }

	render() {
        const { formItemConfig = {}, form } =this.props;
        const { getFieldDecorator } = form;
        const { type, colSpan, label, name, rules, cellStyles = {}, hasLinkage } = formItemConfig;
        const md = colSpan || 8;
        if (!name && type !== 'Btn') {
            return null;
        }
        // 联动显示控制
        if (hasLinkage === '1') {
            const { linkName, condition } = formItemConfig;
            if (form.getFieldValue(linkName) !== condition) {
                return null;
            }
        }
		return (
			<Fragment>
				<Col
			        span={colSpan}
			        md={md} sm={24}
			    >
                    {
                        type !== 'Btn' ?
                            (
                                <Form.Item label={label} style={cellStyles}>
                                    {getFieldDecorator(name, {
                                        rules: rules || [],
                                        initialValue: this.getInitialValue(formItemConfig),
                                    })(
                                        this.renderItem(formItemConfig)
                                    )}
                                </Form.Item>
                            ) : (
                                <Form.Item style={cellStyles}>
                                    {
                                        this.renderItem(formItemConfig)
                                    }
                                </Form.Item>
                            )
                    }
			      
			    </Col>
		    </Fragment>
	    );
	}
}

export default Editable;