import React, { PureComponent, Fragment } from 'react';
import { Divider,Dropdown, Icon, Table } from 'antd';
import { TRUE } from '../../common/Constants';
import TableEdit from '../../EditDrawer/TableEdit';

import './index.css';

class TableContainer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
        };
        this.onShowEditTable = this.onShowEditTable.bind(this);
        this.onClose = this.onClose.bind(this);
        this.onUpdateConfigs = this.onUpdateConfigs.bind(this);
        this.onDeleteContainer = this.onDeleteContainer.bind(this);
    }
    onShowEditTable() {
        this.setState({
            visible: true,
        });
    }
    onClose(e) {
        e.stopPropagation();
        this.setState({
            visible: false,
        });
    }
    onUpdateConfigs(configs) {
        const { layoutIndex, onUpdateLayoutConfig } = this.props;
        onUpdateLayoutConfig({layoutIndex, configs});
        this.setState({
            visible: false,
        });
    }
    onDeleteContainer() {
        const { layoutIndex, onUpdateLayoutConfig } = this.props;
        onUpdateLayoutConfig({ layoutIndex, deleteFlag: true });
    }
    renderMoreOpList(opList) {
        return (
            <span style={{ background: '#fff', display: 'block' }}>
                {
                    opList.map(({opText, index}) => {
                    return (
                        <a href="&nbsp;" key={`op-${index}`} style={{ display: 'block' }}>
                            {opText}
                        </a>
                    );
                    })
                }
            </span>
        );
    }
    renderMultOperation(operationArr) {
        const [ firstItem, ...restItemArr ] = operationArr;
        return (
            <Fragment>
                <a href="&nbsp;">
                    {firstItem.opText}
                </a>
                <Divider type="vertical" />
                <Dropdown overlay={this.renderMoreOpList(restItemArr)}>
                    <a className="ant-dropdown-link" href="&nbsp;">
                        更多<Icon type="down" />
                    </a>
                </Dropdown>
            </Fragment>
        );
    }
    render() {
        const { visible } = this.state;
        const { configs = {} } = this.props;
        const { hasOperation, pagination = false, rowSelection = null,
            operationArr = [], columns } = configs;
        const copyColumns = columns.slice();
        if (hasOperation === TRUE) {
            copyColumns.push({
                title: '操作',
                render: () => {
                    if (!Array.isArray(operationArr)) {
                        return null;
                    }
                    if (operationArr.length > 2) {
                        return (
                            <span>
                                {
                                    this.renderMultOperation(operationArr)
                                }
                            </span>
                        );
                    } else {
                        return (
                            <span>
                                {
                                    operationArr.map(({ opText, index }, i) => {
                                        return (
                                            <Fragment key={`fr-${index}`}>
                                                <a href="&nbsp;" key={`op-${index}`}>{opText}</a>,
                                                {
                                                    i !== operationArr.length -1
                                                        ? <Divider type="vertical" /> : null
                                                }
                                            </Fragment>
                                        );
                                    })
                                }
                            </span>
                        );
                    }
                },
            });
        }
        return (
            <div
                className="table-container container"
            >
                <Table
                    columns={copyColumns}
                    bordered
                    dataSource={[]}
                    pagination={pagination}
                    rowSelection={rowSelection}
                    onClick={this.onShowEditTable}
                />
                <TableEdit
                    visible={visible}
                    configs={configs}
                    onClose={this.onClose}
                    onDeleteContainer={this.onDeleteContainer}
                    onUpdateConfigs={this.onUpdateConfigs}
                />
                <Icon type="form" className="icon-operation" onClick={this.onShowEditTable} />
                {/* <Icon type="close-circle" className="icon-operation" onClick={this.onDeleteContainer} /> */}
            </div>
        );
    }
}

export default TableContainer;